import React from "react";
import Clients from "../images/clients/client-full-list.png";
import { Row, Col } from 'reactstrap';

function FifthPage() {
    return (
        <Row className="section fifth">
            <Col>
                <Row>
                    <Col><span className="title">Who we managed to SAY for</span></Col>
                </Row>
                <Row>
                    <Col>
                        <img src={Clients} />
                    </Col>
                </Row>
            </Col>
        </Row>
    );
}

export default FifthPage;